.section {
  @include css-lock(30, 60, 320, 1499, padding-top);
  @include css-lock(30, 60, 320, 1499, padding-bottom);
  &__title {
    text-align: center;
    margin-bottom: 20px;
    @include md {
      margin-bottom: 10px; } }
  &__title-mb-lg {
    margin-bottom: 60px;
    @include xl {
      margin-bottom: 50px; }
    @include lg {
      margin-bottom: 40px; }
    @include md {
      margin-bottom: 20px; }
    @include sm {
      margin-bottom: 10px; } }
  &__subttl {
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 880px;
    margin-bottom: 60px;
    @include xl {
      margin-bottom: 50px; }
    @include lg {
      margin-bottom: 40px; }
    @include md {
      margin-bottom: 20px; }
    @include sm {
      margin-bottom: 10px; } }

  &--accent-2 {
    background-color: $accent-2;
    box-shadow: inset 0px 0px 20px 0px rgba(0, 0, 0, 0.37); } }
