.testimonial {
  position: relative;
  box-shadow: 0px 3px 9px 0px rgba(0, 0, 0, 0.08);
  border-radius: $radius;
  background-color: $white;
  margin-top: 57px;
  padding: 70px 35px 45px;
  height: calc(100% - 57px);
  &__img {
    position: absolute;
    border-radius: 50%;
    // width: 115px
    // height: 115px
    // border: 5px solid $accent-1
    // +cover
    // background-color: $c-text
    z-index: 1;
    right: 30px;
    top: -57px; }
  &__title {
    margin-bottom: 15px;
    @include md {
      margin-bottom: 10px; } }
  &__text {
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; } } }
