.contacts-block {
  text-align: center;
  background-color: $white;
  box-shadow: 0px 0px 31px 0px rgba(0, 0, 0, 0.15);
  border-radius: $radius;
  padding: 30px 30px 50px;
  &__btn {
    margin-left: auto;
    margin-right: auto;
    max-width: $btn-width; }
  &__title {
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; } } }

.contact-info-block {
  margin-bottom: 50px;
  @include xl {
    margin-bottom: 40px; }
  @include lg {
    margin-bottom: 30px; }
  @include md {
    margin-bottom: 15px; }
  a {
    @include tr(color .3s);
    @include hover {
      color: $accent-1; } }
  &__title {
    margin-bottom: 15px;
    @include md {
      margin-bottom: 10px; } } }

.map {
  position: relative;
  padding-top: 50%;
  background-color: $gray-light;
  iframe {
    @include coverdiv;
    z-index: 1; } }
