.slider {
  &__controls {
    padding-top: 20px;
    text-align: center;
    .prev, .next {
      margin-left: 10px;
      margin-right: 10px; } }
  &__wrap {
    position: relative;
    .tns-nav {
      text-align: center;
      button {
        display: inline-block;
        margin-right: 5px;
        margin-left: 5px;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background-color: $gray-light;
        &.tns-nav-active {
          background-color: $accent-2; } } } }

  &--team {
    &__wrap {
      @include flex(space-between, normal, column);
      @include md {
        display: block;
        .tns-ovh {
          padding-top: 10px;
          padding-bottom: 10px; }
        .tns-outer {
          display: inline-block;
          vertical-align: middle;
          width: calc(100% - 90px); } }
      @include sm {
        .tns-outer {
          width: 100%; } }
      .slider {
        &__prev, &__next {
          margin-left: auto;
          margin-right: auto;
          @include md {
            margin-left: 0;
            margin-right: 0;
            display: inline-block;
            vertical-align: middle; }
          @include sm {
            display: none; } } } } }
  &--items {
    @include flex;
    &__wrap {
      .tns-ovh {
        padding-top: 10px;
        padding-bottom: 10px; } }
    .slide {} } }


.prev, .next {
  display: inline-block;
  position: relative;
  border-radius: 50%;
  background-color: $white;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.08);
  width: 40px;
  height: 40px;
  @include tr(background-color .3s, color .3s);
  @include before {
    @include icon-arrow(10, 10, 1, currentColor, 45);
    @include vcenter;
    transform: translate(-30%, -50%) rotate(45deg); }
  @include hover {
    background-color: $accent-2;
    color: $white; }
  &--vert {
    @include before {
      @include icon-arrow(10, 10, 1, currentColor, 135);
      @include vcenter;
      transform: translate(-50%, -30%) rotate(135deg); }
    @include md {
      @include before {
        @include icon-arrow(10, 10, 1, currentColor, 45);
        transform: translate(-30%, -50%) rotate(45deg); } } } }

.next {
  transform: scaleX(-1);
  &--vert {
    transform: scaleY(-1);
    @include md {
      transform: scaleX(-1); } } }
