.nav {
  color: $c-title;
  ul {
    @include flex(space-between, center);
    @include lg {
      display: block; } }
  li {
    @include lg {
      padding: 10px;
      // height: 41px
      border-bottom: 1px solid $c-text; }
    &.has-dropdown {
      position: relative;
      > a {
        @include after {
          @include icon-arrow(7, 7, 1, currentColor, -45);
          position: relative;
          margin-left: 5px;
          display: inline-block;
          top: -.1em;
          @include lg {
            display: none; } } }
      @include lg {
        padding-right: 50px;
        @include after {
          @include icon-arrow(7, 7, 1, currentColor, -45);
          display: inline-block;
          position: absolute;
          top: 15px;
          right: 17px;
          z-index: 1; }
        @include before {
          height: 42px;
          width: 40px;
          background-color: $accent-2;
          position: absolute;
          top: 0;
          right: 0;
          z-index: 0; } }
      &:hover {
        .nav--dropdown {
          visibility: visible;
          opacity: 1;
          @include lg {
            display: block; } } } } }

  a {
    @include tr(color .3s);
    @include hover {
      color: $accent-1; } }
  .is-active {
    color: $accent-1; } }

.nav--dropdown {
  position: absolute;
  z-index: 1;
  left: 50%;
  transform: translate(-50%, 0);
  top: 100%;
  background-color: transparent;
  width: 250px;
  visibility: hidden;
  opacity: 0;
  padding-top: 20px;
  @include ready {
    @include tr(visibility .3s, opacity .3s, transform .3s); }
  @include lg {
    position: static;
    width: auto;
    margin-left: -10px;
    margin-right: -50px;
    transform: translate(0, 0);
    visibility: visible;
    opacity: 1;
    display: none;
    // max-height: 0
 }    // overflow: hidden

  li {
    display: block;
    @include lg {
      border-bottom: 0; } }
  ul {
    display: block;
    background-color: $accent-1;
    @include lg {
      background-color: $white; } }
  a {
    display: block;
    padding: 10px;
    @include hover {
      background-color: $accent-2;
      color: $white; }
    @include lg {
      padding: 0;
      display: inline; } }
  .is-active {
    background-color: $accent-2;
    color: $white;
    @include lg {
      color: $accent-1;
      background-color: transparent; } } }

.nav--footer {
  color: $white;
  ul {
    display: block; }
  li {
    display: block;
    @include notlast {
      margin-bottom: 15px;
      @include md {
        margin-bottom: 10px; } } }
  a {
    @include hover {
      color: $c-text; } } }
